<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="articleList">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small">新增文章</el-button>
            <div>
                <el-input
                        placeholder="请输入文章标题"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="文章分类"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="文章来源"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>

                <SelectTags v-model="searchParams.tags"></SelectTags>
                <el-button type="success"  size="small" @click="">查询</el-button>
                <el-button type="warning"  size="small" @click="">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="a"
                    label="文章标题"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="b"
                    label="文章分类">
            </el-table-column>
            <el-table-column
                prop="b"
                label="阅读">
            </el-table-column>
            <el-table-column
                prop="b"
                label="点赞">
            </el-table-column>
            <el-table-column
                prop="a"
                label="收藏">
            </el-table-column>
            <el-table-column
                prop="a"
                label="分享">
            </el-table-column>
            <el-table-column
                prop="a"
                label="文章来源">
            </el-table-column>
            <el-table-column
                prop="a"
                label="标签">
            </el-table-column>
            <el-table-column
                prop="a"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="a"
                label="发布时间">
            </el-table-column>
            <el-table-column
                prop="a"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="280">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="success" size="small"
                                >置顶</el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                                >禁用</el-button>
                    <el-button @click="handleClick(scope.row)" type="info" size="small"
                               >预览</el-button>
                    <el-button @click="$router.push({path:'/article/article_list/article_edit'})" type="primary" size="small"
                               >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';
    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination ,SelectTags
        }
    }
</script>
